document.addEventListener('DOMContentLoaded', () => {
  const form = document.querySelector('.form'); // змінено відповідно до HTML
  const inputField = document.querySelector("input[name='searchQuery']");
  const loader = document.querySelector('.loader');
  const gallery = document.querySelector('.gallery');

  if (!form || !inputField || !loader || !gallery) {
    console.error('One or more necessary elements are missing!');
    return;
  }

  form.addEventListener('submit', async event => {
    event.preventDefault();
    const query = inputField.value.trim();

    if (!query) {
      iziToast.error({
        title: 'Error',
        message: 'Please enter a search term!',
        position: 'topRight',
      });
      return;
    }

    gallery.innerHTML = '';
    loader.style.display = 'block';

    try {
      const images = await fetchImages(query);
      console.log(images); // <-- тепер правильно

      if (images.length === 0) {
        iziToast.error({
          message:
            'Sorry, there are no images matching your search query. Please try again!',
          position: 'topRight',
        });
        return;
      }
      renderGallery(images);
    } catch (error) {
      iziToast.error({
        message: 'Something went wrong. Please try again!',
        position: 'topRight',
      });
      console.error('Fetch error:', error);
    } finally {
      loader.style.display = 'none'; // оптимізовано
    }
  });

  inputField.addEventListener('input', () => {
    inputField.style.border = inputField.value.trim()
      ? '2px solid blue'
      : '1px solid #808080';
  });
});
